import uniListItem    from "@/components/uni-ui/uni-list-item/uni-list-item.vue"
import mpvueCityPicker from "@/components/uni-ui/mpvue-citypicker/mpvueCityPicker.vue"
// 引入vuex 
import {mapActions} from "vuex"

export default {
	components:{uniListItem,mpvueCityPicker},
	data() {
		return {
			isedit: false,
			pickerValue: [0, 0, 1],
			themeColor: '#FD6801',
			index: -1, // 存放当前修改对象的索引
			form:{
				path: "",
				name: "",
				phone: "",	
				detailPath: "",
				isDefault: false
			}
			
		}
	},
	computed:{
		pathList: state => state.path.list
	},
	methods: {
		// 引入createPath
		...mapActions(['updatePathAction','createPathAction']),
		// 保存表单
		submit(){
			// 验证表单
			
			if(this.isedit) {
				// 修改数据
				this.updatePathAction({index:this.index, item:this.form})
				uni.showToast({title:"修改成功"})
				uni.navigateBack({delta: 1})
				return
			}
			// 添加数据
			this.createPathAction(this.form)
			uni.showToast({title:"地址添加成功"})
			uni.navigateBack({delta: 1})
		},
		// switch开关事件测试
		test(e){
			// console.log('switch=>e',e);
		},
		// 三级联动取消
		onCancel(e) {
			console.log(e)
		},
		// 提交三级联动选择
		showMulLinkageThreePicker() {
			this.$refs.mpvueCityPicker.show()
		},
		// 三级联动确定
		onConfirm(e) {
			this.form.path = e.label
			this.pickerValue = e.value
		}
	},
	onLoad(e) {
		if(e.data!=="{}") {
			let res = JSON.parse(e.data)
			this.form = res.item
			this.index = res.index
			this.isedit = true
			uni.setNavigationBarTitle({
				title:"修改收货地址"
			})
		}
	},
	// 监听返回
	onBackPress() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel();
			return true;
		}
	},
	// 页面卸载之后关闭选择器
	onUnload() {
		if (this.$refs.mpvueCityPicker.showPicker) {
			this.$refs.mpvueCityPicker.pickerCancel()
		}
	}
}
